// webSocket
import { Toast } from 'vant'
// 
var websock = null
var globalCallback = null
var isClose = false
var timer = null
var timer2 = null
var agentDatas = null
var types = null
var tokens = null
// 
function initWebSocket (agentData, type, token) {
  agentDatas = agentData
  types = type
  tokens = token
  if (!token) return
  if ('WebSocket' in window) {
    isClose = false
    tokens = token
    var typeName = types === 1 ? 'zuqiu' : 'lanqiu'
    var url = `${location.protocol === 'https:' ? 'wss' : 'ws'}://${location.host}/api/ws/live/detail/${typeName}?token=${tokens}`
    console.log(url)
    websock = new WebSocket(url)
    websock.onmessage = (e) => {
      websocketonmessage(e)
    }
    websock.onclose = (e) => {
      websocketclose(e)
    }
    websock.onopen = () => {
      websocketOpen()
      if (agentDatas) {
        websocketsend(agentDatas)
      }
    }
    // 
    websock.onerror = (event) => {
      console.log('WebSocket')
    }
  } else {
    Toast.fail('WebSocket,!')
    return false
  }
}

// 
function websocketonmessage (e) {
  // console.log('', e)
  globalCallback(e)
  // globalCallback(JSON.parse(e.data))
}

// 
function websocketsend (agentData) {
  if (agentData) {
    console.log('')
    if (typeof agentData === 'string') {
      websock.send(agentData)
    } else {
      websock.send(JSON.stringify(agentData))
    }
  }
}

// 
export function websocketclose (e) {
  // console.log(``)
  if (!isClose) { // 
    clearTimeout(timer2)
    timer2 = null
    timer2 = setTimeout(() => {
      initWebSocket(agentDatas, types, tokens)
    }, 1000)
  }
}
export function handleWebsocketClose (e) {
  if (websock) {
    // console.log(``)
    isClose = true
    websock.close()
  }
}
//  websocket 
function websocketOpen (e) {
  console.log('')
}

/**
 * 
 *
 * @export
 * @param {*} agentData
 * @param {*} callback
 * @returns sendSock(data,type,token this.getConfigResult)
 */
export function sendSock (agentData, type, token, callback) {
  if (agentData) {
    globalCallback = callback
    if (websock && websock.readyState === websock.OPEN) {
      // ws
      websocketsend(agentData)
    } else if (websock && websock.readyState === websock.CONNECTING) {
      //  1s
      clearTimeout(timer)
      timer = null
      timer = setTimeout(() => {
        sendSock(agentData, type, token, callback)
      }, 1000)
    } else {
      // 
      initWebSocket(agentData, type, token)
    }
  } else {
    Toast('')
  }
}
